.avatar-bg {
  position: absolute;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100%;
  z-index: 0;
}

.avatar-bg .bar {
  left: 65%;
  height: 240px;
  max-width: 840px;
}

.avatar-bg .avatar-wrap {
  height: 100%;
}

.avatar-bg .avatar {
  max-width: 575px;
  top: 45%;
  left: 70%;
}

@media screen and (min-width: 1001px) and (max-height: 600px) {
  .avatar-bg {
    min-height: 600px;
    height: 600px;
  }
}

@media screen and (max-width: 512px) {
  .avatar-bg {
    margin-top: 60px !important;
  }
}

@media screen and (max-width: 1000px) {
  .avatar-bg {
    position: relative;
    margin-top: 100px;
    left: 0;
    height: 45vh;
    min-height: 300px;
  }

  .avatar-bg .bar {
    left: 50%;
    top: 40%;
    height: 25vw;
    max-height: 200px;
  }

  .avatar-bg .avatar {
    top: 0;
    transform: translate(-50%);
    width: 75vw;
    height: 45vh;
    min-height: 300px;
    left: 50%;
    max-width: 400px;
  }
}

@media screen and (min-width: 1001px) and (max-width: 1200px) {
  .avatar-bg {
    left: 0;
  }

  .avatar-bg .bar {
    left: 60%;
    max-width: 65vw;
    height: 200px;
    max-height: 240px;
  }

  .avatar-bg .avatar {
    max-width: 485px;
    width: 50vw;
    left: 70%;
  }
}

.u-vhide {
  overflow: hidden;
  position: absolute;
  clip: rect(0, 0, 0, 0);
  left: -900px;
}

@media screen and (max-width: 512px) {
  .u-hide\:s {
    display: none;
  }
}

@media screen and (min-width: 513px) and (max-width: 1000px) {
  .u-hide\:m {
    display: none;
  }
}

@media screen and (min-width: 1001px) {
  .u-hide\:l {
    display: none;
  }
}

*,
:after,
:before {
  box-sizing: border-box;
}

main:focus {
  outline: none;
}

html {
  font-size: 62.5%;
  -webkit-font-smoothing: antialiased;
}

html:after,
html:before {
  content: "";
  will-change: top left;
  backface-visibility: hidden;
  position: fixed;
  top: 0;
  height: 100%;
  width: 20px;
  background: #fff;
  z-index: 21000;
}

html:before {
  left: 0;
}

html:after {
  right: 0;
}

body {
  background: var(--bg);
  min-height: calc(100vh - 40px);
  position: relative;
  padding: 20px;
}

body:after,
body:before {
  content: "";
  will-change: top left;
  backface-visibility: hidden;
  position: fixed;
  left: 0;
  width: 100%;
  height: 20px;
  background: #fff;
  z-index: 20000;
}

body:before {
  top: 0;
}

body:after {
  bottom: 0;
}

body.lock-scroll,
html.lock-scroll {
  overflow: hidden;
}

@media (max-width: 512px) {
  html:after,
  html:before {
    width: 10px;
  }

  body {
    padding: 10px;
  }

  body:after,
  body:before {
    height: 10px;
  }
}

.inner-body {
  max-width: 975px;
  margin: 0 auto;
}

.pagewrap {
  min-height: calc(100vh - 40px);
  position: relative;
  background: var(--bg);
  overflow: hidden;
  transition: background 1s cubic-bezier(0.165, 0.84, 0.44, 1);
}

@media (max-width: 511px) {
  .pagewrap:before {
    content: "";
    position: fixed;
    background: linear-gradient(180deg, var(--bg) 60%, hsla(0, 0%, 100%, 0));
    width: 100%;
    height: 100px;
    top: 0;
    left: 0;
    pointer-events: none;
    z-index: 999;
  }
}

.chunk {
  margin: 150px auto;
}

.chunk:first-child {
  margin-top: 0;
}

@media (min-width: 741px) {
  .break-out {
    width: 75vw;
    margin-top: 60px;
    margin-bottom: 60px;
    margin-left: 50%;
    transform: translateX(-50%);
  }
}

.page-body {
  margin: 100px 20px;
}

@media (min-width: 512px) {
  .page-body {
    margin: 230px 30px 100px;
  }
}

@media (min-width: 800px) {
  .page-body {
    margin: 230px 60px 100px;
  }
}

.page-body--header {
  margin: 0 20px 100px;
}

@media (min-width: 512px) {
  .page-body--header {
    margin: 0 60px 100px;
  }
}

hr {
  height: 1px;
  border: none;
  background: var(--text);
  opacity: 0.275;
  margin: 100px auto;
}

.section {
  max-width: 1200px;
  margin: 0 auto;
}

.article {
  max-width: 740px;
  margin: 0 auto;
}

.cluster,
.repel {
  display: flex;
  align-items: center;
  color: inherit;
}

.cluster + pre[class*="language-"],
.repel + pre[class*="language-"] {
  margin-top: 25px;
}

.repel {
  justify-content: space-between;
}

.cluster {
  justify-content: space-around;
}

.mainnav {
  z-index: 10000;
  display: inline-block;
  vertical-align: middle;
  position: fixed;
  width: 200px;
  top: 0;
  left: 35px;
}

@media (min-width: 512px) {
  .mainnav {
    top: 30px;
    left: 65px;
  }
}

.logo-wrap {
  position: relative;
  z-index: 10000;
  display: inline-block;
  vertical-align: middle;
  max-width: 65px;
  padding-top: 8px;
  width: 100%;
}

@media (min-width: 512px) {
  .logo-wrap {
    padding-top: 0;
    max-width: 100px;
  }
}

.logo-wrap .logo {
  position: relative;
  display: block;
  animation: logoIn 0.45s ease-out 0.2s backwards;
}

.logo-wrap .logo .shade {
  transition: transform 1.5s ease-out;
  backface-visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.logo-wrap svg {
  max-width: 100%;
  height: auto;
  margin-left: -10px;
}

.svg-stroke {
  fill: none;
  stroke: var(--line);
  stroke-width: 2.5px;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-miterlimit: 10;
  vector-effect: non-scaling-stroke;
}

@media (min-width: 512px) {
  .svg-stroke {
    stroke-width: 3px;
  }
}

.svg-fill-stroke {
  fill: var(--line);
}

.svg-fill {
  fill: var(--fill);
}

.svg-blank {
  fill: none;
}

.stroke-path,
.stroke-poly {
  fill: none;
  stroke: var(--line);
  stroke-width: 2.5px;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-miterlimit: 10;
  vector-effect: non-scaling-stroke;
}

@media (min-width: 512px) {
  .stroke-path,
  .stroke-poly {
    stroke-width: 3px;
  }
}

.svg-stroke-thin {
  fill: none;
  stroke: var(--line);
  stroke-width: 2.5px;
  stroke-linecap: round;
