.circle {
  max-width: 400px;
  margin: 0 auto;
}

.circle__inr {
  position: relative;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 100%;
  animation: bakedAvatar 1.125s cubic-bezier(0.215, 0.61, 0.355, 1) 0.25s
    backwards;
  animation-play-state: paused;
}

.circle__inr svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.circle__inr.is-active,
.circle__inr.is-active .circle__bg:after,
.no-js .circle__inr,
.no-js .circle__inr .circle__bg:after {
  animation-play-state: running;
}

.circle__bg {
  top: 5%;
  width: 88%;
  height: 88%;
  transition: all 1.5s ease-out;
  background-repeat: no-repeat;
}

.circle__bg,
.circle__bg:after {
  position: absolute;
  border-radius: 100%;
}

.circle__bg:after {
  content: "";
  background: inherit;
  background-repeat: repeat;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  animation: circleIn 0.5s cubic-bezier(1, 1.6, 0, 1.66) backwards;
  animation-play-state: paused;
}

@keyframes circleIn {
  0% {
    transform: scale(0);
    opacity: 1;
  }

  to {
    transform: scale(1);
    opacity: 1;
  }
}

.iconwrap,
.illustration__layer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.code {
  overflow: hidden;
  width: 21%;
  height: 14%;
  top: 57%;
  left: 19%;
}

.code,
.code__inr {
  position: absolute;
}

.code__inr {
  height: 200vh;
  width: 100%;
  top: 0;
  left: 0;
  background-size: 100% auto;
  background-repeat: repeat-y;
}

.pointer {
  backface-visibility: hidden;
  position: absolute;
  height: 27%;
  width: 46%;
  top: 53%;
  left: 11.5%;
  transition: transform 0.125s ease-out;
}

.pointer svg polygon {
  stroke: var(--line);
  fill: var(--line);
}

@keyframes bob {
  0% {
    transform: translateY(-2.5%);
  }
}

@keyframes bob2 {
  to {
    transform: translateY(-2.5%);
  }
}

.iconwrap--a {
  animation: bob 4s ease-in-out infinite alternate;
}

.iconwrap--b {
  animation: bob2 5s ease-in-out infinite alternate;
}

